import React from "react";
import Card from "./Card";



//class statejust lazy
class CardHolder extends React.Component{
    constructor(props){
        super(props)
    }
    
    render(){
        const listRender = this.props.word.map(
            (item,key)=>{
                //map
                return(
                    <Card 
                        key={item} w={item}
                        num = {this.props.num}
                        // 
                        destroy={this.props.destroy}
                        // 
                        upDown={this.props.d}
                        // (boolean)   
                        border={this.props.isHighLight?((parseInt(this.props.order)===this.props.num)?(parseInt(this.props.which)===key):false):false}
                        //
                        mark = {this.props.mark}
                        demark = {this.props.demark}
                        //
                        image = {this.props.image[key]}
                        //0 1
                        mode = {1}
                        canDrag = {true}
                        showZone = {this.props.showZone}
                        closeZone = {this.props.closeZone}
                    />
                );
            }
        )
        return(
            <div style={{display:"flex",flexDirection:"row",alignItems:this.props.d,height:'295'}}>
                {listRender}
            </div>
        )
    }
}
export default CardHolder;